.home {
  display: flex;
  width: 80rem;
  max-width: 90vw;
  margin: 0 auto;
}
.magicContainer {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 0 1rem;
}
.magicBar {
  background: linear-gradient(
    180deg,
    rgba(115, 211, 147, 0.5) 0%,
    rgba(245, 188, 69, 0.5) 34.38%,
    rgba(199, 107, 107, 0.5) 65.1%,
    rgba(211, 115, 190, 0.5) 100%
  );
  min-width: 4px;
  max-width: 4px;
  height: 100%;
  border-radius: 1rem;
  position: absolute;
}
.magicTracker {
  top: 10vh;
  position: sticky;
  min-width: 0.5rem;
  min-height: 1rem;
  background: #73d393;
  border-radius: 100%;
  box-shadow: 0 0 10px 3px black;
}

.home-content {
  flex: 1;
}

/* PRESENTATION */
.home-section {
  padding-top: calc(10vh - 2rem);
  position: relative;
  margin-left: 2rem;
  margin-right: 2rem;
  height: 100vh;
}
.home-section-icon {
  border-radius: 10rem;
  padding: 1rem;
  background: #101010
    radial-gradient(
      circle at 50% 50%,
      rgb(115, 211, 147, 0.5) 0%,
      rgb(115, 211, 147, 0.2) 30%,
      rgb(115, 211, 147, 0.1) 50%,
      rgb(115, 211, 147, 0) 70%
    );
  position: absolute;
  display: grid;
  place-items: center;
  right: calc(100% + 1.25rem);
  top: calc(10vh - 1.75rem);
}
.presentation {
  height: 75vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.presentation p {
  margin-bottom: 5rem;
}
.presentation h1 {
  color: #73d393;
}
.skill-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 2rem;
}

.skill {
  background-color: #232323;
  box-shadow: inset 1px 1px 4px #101010;
  border-radius: 1.5rem;
  height: 300px;
  width: 300px;
  padding: 1rem 2.5rem;
  box-sizing: border-box;
}
